 de comercio en medios electrónicos, es necesario que las Cámaras de Comercio implementen dentro de sus servicios virtuales, en los términos y condiciones señalados en las normas que rijan la materia, la posibilidad de efectuar el registro de estos libros, para lo cual deberán habilitar en sus plataformas electrónicas o sistemas de información, dicho servicio, garantizando su disponibilidad y fácil acceso para su posterior consulta. Las Cámaras de Comercio deberán dar publicidad al servicio de registro de libros de comercio en medios electrónicos y establecer los controles respectivos que impidan el registro en forma simultánea de un mismo libro, en medios electrónicos o de forma física, a fin de evitar su duplicidad. Las Cámaras de Comercio podrán facilitar la utilización de los mecanismos de firma digital o electrónica, no obstante, corresponde al comerciante la elección de cualquiera de estos siempre y cuando cumplan con los requisitos previstos en las normas que rijan la materia. El servicio de registro de libros en medios electrónicos deberá estar disponible para las entidades sin ánimo de lucro inscritas en las Cámaras de Comercio, en los mismos términos y condiciones de las sociedades comerciales, de acuerdo con las normas que rijan esta materia. ")])]),_c('div',{attrs:{"titulo":"2.1.9.2.2. Procedimiento para efectuar registro de los libros de comercio registrables en medios electrónicos"}},[_c('p',[_vm._v("De conformidad con lo establecido en la ley, los comerciantes que quieran llevar sus libros de comercio registrables, en medios electrónicos, deberán solicitarlo de manera expresa ante la Cámara de Comercio correspondiente a su domicilio, indicando una dirección de correo electrónico a la cual se le puedan remitir las inscripciones efectuadas y aceptando los términos y condiciones por ella establecidas para el efecto. Una vez recibida la solicitud de inscripción de los libros registrables, por parte del interesado, la Cámara de Comercio correspondiente deberá efectuar una inscripción por cada uno de los libros en el Libro respectivo del registro de que se trate, debiendo devolver al solicitante el archivo electrónico al correo electrónico que tenga reportado el comerciante o inscrito. Para ello, la Cámara de Comercio deberá firmarlo digital o electrónicamente, dejando constancia electrónica de la fecha y la hora en que fue enviado o remitido el archivo, por cualquier medio tecnológico disponible. De conformidad con lo establecido en las normas que rigen la materia, la constancia electrónica expedida por la Cámara de Comercio correspondiente, deberá tener la siguiente información: – Cámara de Comercio receptora. – Fecha de presentación del libro para registro. – Fecha de inscripción. – Número de inscripción. – Identificación del comerciante o persona obligada a registrar. – Nombre del libro, y – Uso al que se destina. A su vez, si a la fecha de la solicitud de inscripción del libro registrable por medios electrónicos, el libro físico que lo antecede posee hojas que no hubieren sido empleadas, deberán ser anuladas, de acuerdo con lo previsto en las normas que rigen la materia. ")])]),_c('div',{attrs:{"titulo":"2.1.9.2.3. Conformación de los libros registrados en medios electrónicos "}},[_c('p',[_vm._v("Efectuada la inscripción del libro de actas de juntas de socios o accionistas en medios electrónicos, el comerciante o inscrito tendrá derecho a remitir a la Cámara de Comercio, por el término de un (1) año, los archivos electrónicos en donde consten las actas de dicho órgano, los cuales deberán ser firmados digital o electrónicamente por quienes actuaron como presidente y secretario de la reunión. La solicitud de asentar el archivo electrónico contentivo del acta, en el libro correspondiente, deberá estar suscrita digital o electrónicamente por el representante legal, presidente o secretario. Efectuada la inscripción del libro de socios o accionistas en medios electrónicos, el comerciante o inscrito tendrá derecho a remitir a la Cámara de Comercio, por el término de un (1) año, archivos electrónicos destinados a ese libro, los cuales deberán ser firmados digital o electrónicamente por el representante legal. Es responsabilidad de cada comerciante la provisión de las firmas digitales o electrónicas y, estampas cronológicas necesarias. Una vez recibidos los archivos electrónicos, la Cámara de Comercio correspondiente deberá devolver al solicitante el archivo electrónico al correo electrónico que tenga reportado el comerciante o inscrito. Para ello, la Cámara de Comercio deberá firmarlo digital o electrónicamente, dejando constancia electrónica de la fecha y la hora en que fue enviado o remitido el archivo por cualquier medio tecnológico disponible. ")])])]),_vm._m(23),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-left"}},[_vm._v("Son los libros que reflejan de forma ordenada las operaciones realizadas por la empresa, afectando los débitos y créditos de una o varias cuentas y subcuentas, con el fin de generar el saldo en una fecha determinada.")]),_vm._m(24),_vm._m(25),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-left"}},[_vm._v("El Estado colombiano busca, mediante la Resolución 8934 de febrero de 2014, implementar el sistema de gestión documental, con el objetivo de mantener organizados los archivos en el sector privado, enfocada básicamente en la organización de:")]),_vm._m(26),_c('div',{staticClass:"row justify-content-center align-items-stretch mb-3"},[_vm._m(27),_vm._m(28),_c('div',{staticClass:"col-lg-3 mb-3 mb-lg-0"},[_c('a',{staticClass:"anexo mb-4 bg-color-8",attrs:{"href":_vm.obtenerLink('/downloads/Resolucion_8934_2014.pdf'),"target":"_blank"}},[_vm._m(29),_vm._m(30)])])])],1)],1)
